import { NativeService } from './../../../services/native-service';
import { StorageService } from './../../../services/storage-service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';

/**
 * Generated class for the CommunityOptionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
export interface CommunityMyOption{
  follows: string,
  publish: string,
  comments: string,
  avtivity: string
}

export interface CommunityUrls{
    clubDetail: string,
    activity: string,
    Posts: string,
    more: string,
    my: CommunityMyOption
}
@IonicPage()
@Component({
  selector: 'page-community-option',
  templateUrl: 'community-option.html',
})
export class CommunityOptionPage {

  tmpUrl: string = 'http://www.popochiu.com/upload/1558145430468.html'
  CommunityUrl: string = 'http://www.popochiu.com/community/index.html'

  my: CommunityMyOption = {
      follows: this.tmpUrl,
      publish: this.tmpUrl,
      comments: this.tmpUrl,
      avtivity: this.tmpUrl
    }
  CommunityUrls: CommunityUrls = {
      clubDetail: this.tmpUrl,
      activity: this.tmpUrl,
      Posts: this.tmpUrl,
      more: this.tmpUrl,
      my: this.my
    }
  result: string = ""
  array: any = []
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public storage: StorageService,
    public events: Events,
    public native: NativeService) {
    this.init()
  }
  async init() {
    let CommunityUrl = await this.storage.getItem('CommunityUrl');

    let CommunityUrls = await this.storage.getItem('CommunityUrls');
    if (CommunityUrls) { 
      this.CommunityUrls = CommunityUrls
    }
    this.CommunityUrl = CommunityUrl || 'http://www.popochiu.com/community/index.html'
  }

  changeCommunityUrl() { 
    this.result = JSON.stringify(this.CommunityUrls, null, 2)
    this.storage.setItem('CommunityUrls', this.CommunityUrls);
    this.storage.setItem('CommunityUrl', this.CommunityUrl);
    this.events.publish('changeCommunityUrl', this.CommunityUrl)

    this.native.showToast('Ok')
  }
}



